<template>
  <def-layout>
    <h3 slot="layout_title">新增报价单</h3>

    <div slot="layout_inner_head">

      <h5 class="fw-none mt-2">
        选择菜品
      </h5>

      <table class="table m-0">
        <tr class="text-left">
          <td width="10%">序号</td>
          <td width="15%">货号</td>
          <td width="30%">描述</td>
          <td width="25%">单价</td>
          <td width="10%"></td>
          <td width="10%" class="text-center">选择操作</td>
        </tr>
      </table>
    </div>

    <prices-plus-body slot="layout_inner_body"
      :selected="select"
      :prices_contented="price_content"
      @removeItemChild="removeItem"
      @addItemChild="addItem"
      @changeNum_Father="changeNum"
      v-if="price_content.length > 0"
    >
    </prices-plus-body>

    <div slot="layout_footer">
      <prices-plus-footer 
        :membery="member"

        @reciverTimed_Father="reciverTimed"
        @reciverNumed_Father="reciverNumed"

        @changeMember_Father="changeMember"
        @pricesSubmit_Father="pricesSubmit"
        @pricesPdfView_Father="pricesPdfView"
      ></prices-plus-footer>
    </div>

    <div slot="layout_extra">
      <member-choise-modal @returnChoised="reciverChoised"></member-choise-modal>
      <prices-pdf-modal 
      
        :the_member="member" 
        :the_prices_timed="timed_prices" 
        :the_prices_numed="numed_prices"
        :the_prices_content="select_content"
        :the_prices_id="null"
      >
      </prices-pdf-modal>
    </div>

  </def-layout>
</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'

import DefLayout from '@/components/Layout/DefLayout.vue'
import PricesPlusBody from './Body/PricesPlusBody.vue'

import conn from '@/service/conn'
import MemberChoiseModal from '@/components/Modal/Choise/MemberChoiseModal.vue'

import PricesPlusFooter from './Footer/PricesPlusFooter.vue'
import PricesPdfModal from '@/components/Modal/Pdf/PricesPdfModal.vue'

export default {
  components: { DefLayout, PricesPlusBody, MemberChoiseModal, PricesPlusFooter, PricesPdfModal },
  name: 'PricesPlus',
  data() {
    return {
      price_content: [],
      select: [], // id
      select_content: [],

      member: null,
      numed_prices: '',
      timed_prices: ''
    }
  },
  created() {

  },
  mounted() {
    // 验证 客户信息
    if (this.member == null) {
      const active_member = JSON.parse(tool.storage._get('prices_active_member'))

      if ((active_member != null) && (active_member != '') && (active_member != undefined)) {
        this.member = active_member
      } else {
        tool.showModal('memberChoiseModal')
      }
    }

    // 加载 价格内容
    conn.freight.getList_Freight().then(res => {

      this.price_content = []
      for (const f of res) {
        
        this.price_content.push(
          {
            id: f.id,
            num: f.num,
            named: f.named,
            unit: f.unit,
            price: f.price,
          }
        )
      }
    })
    
  },
  methods: {
    // 后台事件
    _collect() {
      this.select_content = this.price_content.filter((e) => tool.inList(e.id, this.select))
    },
    _submit() {
      this._collect()

      const data = {
        num: this.numed_prices,
        start_time: this.timed_prices + ' 00:00:00',
        draft_status: 'False',
        membery_id: this.member.id,
        freight_num: this.select_content.length
      }
      
      conn.prices.add(data).then(res => {
        
        if (conn.judge_response(res)) {

          conn.prices_content.add({
            price_collect_id: res.id,
            content: JSON.stringify(this.select_content)
          }).then(res => {

            if (conn.judge_response(res)) {

              tool.showToast('新增成功！！！')
              setTimeout(() => {
                this.$router.push('/home/prices')
              }, 700)
            }
          }).catch(err => {
            tool.showToast('因网络波动，该报价单仅仅存为草稿！')
          })
        }
      }).catch(err => {
        tool.showToast('报价单保存失败，请检查输入字段的正确性！')
      })
      
    },
    // 界面操作事件
    pricesPdfView() {
      this._collect()
      tool.showModal('pricesPdfViewModal')
    },
    pricesSubmit() {
      let res = ''
      if (this.timed_prices == '') {
        res = '请检查报价时间的输入！'
        // tool.focus('input_prices_timed')
      }
      if (this.numed_prices == '') {
        res = '请检查报价单号的输入！'
      }
      if (res != '') { tool.showToast(res) } else {
        this._submit()
      }
    },
    //
    changeMember() {
      tool.showModal('memberChoiseModal')
    },
    removeItem(id) {
      const i = tool.indexList(id, this.select)
      if (this.select.length < 2) {
        tool.showToast('至少选择一项菜品。')
      } else {
        this.select.splice(i, 1)
      }
    },
    addItem(id) {
      const i = tool.indexList(id, this.select)
      if (i < 0) {
        this.select.push(id)
      }
    },
    changeNum(index, v) {
      this.price_content[index].price = v
    },
    // 数据接收事件
    reciverChoised(v) {
      this.member = v
      tool.storage._set('prices_active_member', JSON.stringify(v))
    },
    reciverTimed(v) {
      this.timed_prices = v
    },
    reciverNumed(v) {
      this.numed_prices = v
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/content.sass"

</style>


